import React from "react";
import { Tabs,Button,message,Modal,Select } from 'antd';
import Style from "./Layout.less";
import {inject, observer} from 'mobx-react';
import Pane from "./components/Pane"
import AddProjectModal from "./components/AddProjectModal"
import EditProjectModal from "./../project/components/EditProjectModal"
import fetchApi from '../../api'
const TabPane=Tabs.TabPane;
const Option=Select.Option;

@inject("projectList","project","doc")
@observer
class Layout extends React.Component {
  state={
    addProjectModalShow:false,
    editProjectModalShow:false,
    interfaseList:[],
    tabsKey:"all"
  }
  componentDidMount(){
    this.fetchProjectList()
  }
  openEditProjectModal=(projectId)=>{
    this.props.project.getProjectInfo(projectId).then(()=>{
      this.setState({editProjectModalShow:true})
    })

  }
  handleUpdateProjectOk=(info)=>{
    this.props.project.updateProject(this.props.project.info.id,info).then(()=>{
      this.fetchProjectList()
    })
  }
  closeEditProjectModal=()=>{
    this.setState({editProjectModalShow:false})
  }
  handleChange=(key)=>{
    this.setState({"tabsKey":key},()=>{
      this.fetchProjectList()
    });
  }

  handleDeleteDoc=(docId)=>{
    Modal.confirm({
    title: '',
    content: '',
    onOk:()=> {
      this.props.doc.deleteDoc(docId).then(()=>{
        message.success('')
        this.fetchProjectList()
      })
    },
  });
  }

  fetchProjectList(){
    if(this.state.tabsKey==="self"){
      this.props.projectList.getSelfProjectList()
    }else if(this.state.tabsKey==="relate"){
      this.props.projectList.getRelateProjectList()
    }else if(this.state.tabsKey==="all"){
      this.props.projectList.getProjectList()
    }else if(this.state.tabsKey==="develop"){
      this.props.projectList.getDevelopProjectList()
    }
  }

  handleAddProject=()=>{
    this.setState({addProjectModalShow:true})
  }

  handleAddProjectOk=(info)=>{
    this.props.projectList.addProject(info).then(()=>{
      message.success("");
      this.props.projectList.getSelfProjectList()
    })
  }
  handleAddProjectClose=()=>{
    this.setState({addProjectModalShow:false})
  }
  handleShowMockUrl=(url)=>{
    Modal.info({
     title: 'mock',
     content: url+" + url",
   });
  }
  handleDeleteProject=(projectId)=>{
    Modal.confirm({
    title: '',
    content: '',
    onOk:()=> {
      this.props.projectList.delProject(projectId).then(()=>{
        message.success('')
        this.props.projectList.getSelfProjectList()
      })
    },
  });
  }

  handleSearch=(value)=>{
    this.props.history.push("/project/"+value);
  }

  handleSearchInterfase=(text)=>{
    fetchApi.fetchSearchInterfase(text).then(data=>{
      this.setState({interfaseList:data});
    })
  }

  render(){
    return (
      <div >
        <EditProjectModal  onOk={this.handleUpdateProjectOk} onClose={this.closeEditProjectModal}  visible={this.state.editProjectModalShow} ></EditProjectModal>
        <AddProjectModal  onOk={this.handleAddProjectOk} onClose={this.handleAddProjectClose}  visible={this.state.addProjectModalShow}></AddProjectModal>
        <Tabs activeKey={this.state.tabsKey} onChange={this.handleChange} tabBarExtraContent={<Button onClick={this.handleAddProject}></Button>}>
        <TabPane tab="" key="all">
            <div>
            <Select
              showSearch
              style={{ width: 300 }}
              placeholder=""
              optionFilterProp="children"
              onChange={this.handleSearch}
              filterOption={(input, option) =>{
                return option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
              }
            > 
            {this.props.projectList.all.map(item=><Option key={item.id} value={item.id}>{item.name}</Option>)}
              
            </Select>
            &emsp;

            <Select
              showSearch
              style={{ width: 300 }}
              placeholder="url"
              onChange={(e,option)=>{
                this.props.history.push(`project/${option.props.projectid}?moduleId=${option.props.moduleid}&interfaseId=${option.props.interfaseid}`);
              }}
              onSearch={this.handleSearchInterfase}
              filterOption={(input, option) => {
                return option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0 || option.props.value.toLowerCase().indexOf(input.toLowerCase()) >= 0 
              }}
            >
              {this.state.interfaseList.map(item=>(
                <Option moduleid={item.moduleId} interfaseid={item.id} projectid={item.projectId} key={item.id} value={item.url+item.id}>{item.name}</Option>
              ))}
              
            </Select>
            <br/>
            <br/>
            </div>
            {this.props.projectList.all.map(project=>(
              <Pane onMockUrl={this.handleShowMockUrl} key={project.id} project={project}></Pane>
            ))}
            {this.props.projectList.all.length===0&&<div className={Style.noDataNote}></div>}
          </TabPane>
          <TabPane tab="" key="self">
            {this.props.projectList.self.map(project=>(
              <Pane onMockUrl={this.handleShowMockUrl} onDelete={this.handleDeleteProject} onUpdate={this.openEditProjectModal} onDeleteDoc={this.handleDeleteDoc} self editable key={project.id} project={project}></Pane>
            ))}
            {this.props.projectList.self.length===0&&<div className={Style.noDataNote}></div>}
          </TabPane>
          <TabPane tab="" key="develop">
            {this.props.projectList.develop.map(project=>(
              <Pane onMockUrl={this.handleShowMockUrl} onDeleteDoc={this.handleDeleteDoc} onUpdate={this.openEditProjectModal} editable key={project.id} project={project}></Pane>
            ))}
            {this.props.projectList.develop.length===0&&<div className={Style.noDataNote}></div>}
          </TabPane>
          <TabPane tab="" key="relate">
            {this.props.projectList.relate.map(project=>(
              <Pane onMockUrl={this.handleShowMockUrl} key={project.id} project={project}></Pane>
            ))}
            {this.props.projectList.relate.length===0&&<div className={Style.noDataNote}></div>}
          </TabPane>
          
        </Tabs>
      </div>
    )
  }
}

export default Layout



